/*------------------------------------------------------------------
  [Page aside]
*/
.be-aside {

	.main-content {
		margin-left: $page-aside-width;
    width: auto;

		@include tablet {
			margin-left: inherit;
    }
    
    @media #{$print} {
      margin-left: 0;
    }
	}

  .be-content {
    height: 100%;
  }

  .content {
    height: 100%;
  }
}

.page-aside {
  background: none repeat scroll 0 0 $page-aside-bg;
  width: $page-aside-width;
  height: 100%;
  position: fixed;
  top: 0;
  left: $left-sidebar-width;
  border-right: 1px solid darken(theme-color("light"), 10%);
  margin-top: $top-header-height + 1px;
	padding-bottom: $top-header-height + 1px;
  color: $page-aside-text-color;

  @media #{$print} {
		display: none;
	}

  .be-collapsible-sidebar-collapsed & {
    left: $left-sidebar-collapsed-width;

    @include tablet {
      left: 0;
    }
  }

  .no-border & {
    border-width: 0;
  }

  .be-scroller-aside {
    position: relative;
    height: 100%;
    width: 100%;
  }

  .content {
  	padding: 20px $page-aside-padding;
    position: relative;
  }

	@include tablet {
    width: 100%;
    display: block;
    height: auto;
    position: relative;
    margin-top: 0;
    padding-bottom: 0;
    left: 0;
    border-bottom: 2px solid darken(theme-color("light"), 5%);
	}
}

.aside-header {
  margin-bottom: 5px;

  .title {
    display: block;
    margin: 16px 0 0;
    font-size: $page-aside-title-size;
    line-height: $page-aside-title-size;
    font-weight: $font-weight-light;
  }

  .navbar-toggle {
    background:rgba(0, 0, 0, 0);
    display: none;
    outline: none;
    border: 0;

    padding: 0 11px 0 0;
    text-align: right;
    margin: 0;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    position: absolute;

    @include tablet {
      display: block;
    }

    .icon {
      font-size: 1.538rem;
      color: lighten($body-color, 50%)
    }
  }

  .description {
    color: lighten($body-color, 10%);
    margin: 0;
  }
}

.aside-nav {
  visibility: visible;

  &.collapse {
    display: block;
  }

  .nav {

    li {

      a {
        color: $body-color;
        padding: 13px $page-aside-padding;
      }

      &.active {
        
        a {
          color: theme-color("primary");

          .icon {
            color: theme-color("primary");
          }
        }
      }

      .badge {
        float: right;
        font-size: .9231rem;
        font-weight: $font-weight-light;
        padding: 1px 7px;
      }

      .icon {
        font-size: 1.462rem;
        vertical-align: middle;
        text-align: center;
        min-width: 19px;
        margin-right: 6px;
        color: lighten($body-color, 13%);
      }
    }
  }

  .title {
    display: block;
    color: lighten($body-color, 35%);
    font-size: .8462rem;
    font-weight: $font-weight-bold;
    text-transform: uppercase;
    margin: 20px 0 0;
    padding: 8px $page-aside-padding 4px;
  }

  @include tablet {
    border-bottom: 1px solid darken(theme-color("light"), 10%);

    &.collapse {
      display: none;
    }

    &.show {
      display: block;
    }
  }
}

