// Header

//Email components

//==Email inbox page
//Inbox header
.be-aside-header {
  background-color: theme-color("light");
  padding: 37px 25px;

  @include laptop-only {
    padding: 20px 25px;
  }
}

//Inbox title
.be-aside-header-title {
  display: block;
  margin: 3px 0 0;
  font-size: 1.769rem;
  font-weight: $font-weight-light;

  @include laptop {
    margin-bottom: 10px;
  }

  .icon {
    font-size: 1.769rem;
    color: lighten($body-color, 20%)
  }

  .new-messages {
    font-size: $font-size-base;
    color: lighten($body-color, 30%);
    margin-left: 3px;
  }
}

//Inbox filters
.be-aside-header-filters {
  padding: 0 20px 30px;
  border-bottom: 1px solid darken(theme-color("light"), 10%);
  background-color: theme-color("light");
  display: table;
  width: 100%;

  .dropdown-toggle {

    &:after {
      display: inline-block;
    }
  }

  > div {
    display: table-cell;
    vertical-align: middle;

    @include laptop {
      vertical-align: top;
    }
  }

  .btn-group + .btn-group {

    @include laptop {
      margin-left: 0;
    }
  }

  .be-select-all.be-checkbox {
    display: inline-block;
    vertical-align: middle;
    padding: 0;

    label:before {
      margin-right: 18px;
    }
  }

  .be-aside-header-filters-right {
    text-align: right;
  }

  input {
    margin-right: 8px;
  }
}

//Pagination text
.be-aside-pagination-indicator {
  display: inline-block;
  vertical-align: middle;
  margin-right: 13px;

  @include laptop-only {
    margin-right: 0;
  }
}

//Pagination buttons nav
.be-aside-pagination-nav {

  .btn {
    min-width: 30px;

    i {
      font-size: 1.615rem;
      vertical-align: middle;
    }
  }
}
